.content {
  float: left;
  width: calc((100% - 2rem) / 3);
  height: 100%;
  padding: 1rem;
  box-sizing: border-box;
  background-color: #fff;

  & + & {
    margin-left: 1rem;
  }

  .content__title {
    margin: 0;
    font-size: 1rem;
  }

  .content__main {
    height: calc(100% - 21px);
  }
}
