.nav__item {
  border-bottom: 1px solid #213049;

  .nav__btn {
    width: 100%;
    padding: 0;
    border: 0;
    background-color: transparent;
    color: #fff;
    text-align: left;
    cursor: pointer;

    // &:hover,
    // &:focus,
    &:active {
      background-color: #5cbcf0;
      font-weight: bold;
    }

    .nav__name {
      display: block;
      position: relative;
      padding: 0.8rem 2rem 0.8rem 2.5rem;
      font-size: 1rem;

      svg {
        position: absolute;
        top: 50%;
        right: 87%;
        width: 20px;
        height: 20px;
        transform: translateY(-50%);
      }

      div {
        svg {
          right: 12%;
        }
      }
    }
  }

  .nav__btn--isClicked {
    background-color: #5cbcf0;
    font-weight: bold;
  }
}
