$bg-color: #1a93e5;

.header {
  height: 4rem;
  background-color: $bg-color;

  .header__wrap {
    position: relative;
    float: left;
  }

  .header__btn {
    display: block;
    float: left;
    width: 4rem;
    height: 100%;
    padding: 0;
    border: 0;
    background-color: transparent;
    font-size: 1rem;
    color: #fff;
    cursor: pointer;

    &:hover,
    // &:focus,
    &:active {
      background-color: #0d86d8;
      color: #ddd;
    }
  }

  .header__btn--SVG {
    padding: 1rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  @mixin text($height) {
    display: block;
    padding: 0 1rem;
    height: 100%;
    line-height: $height;
  }

  .header__btn--link {
    width: auto;

    .header__link {
      @include text(4rem);
      color: #fff;
      text-decoration: none;
    }
  }

  .header__btn--dropdown {
    width: auto;

    .header__text {
      @include text(4rem);

      svg {
        transform: translateY(10%);
      }
    }
  }

  .header__btn--right {
    float: right;
  }

  &::after {
    display: block;
    clear: both;
    content: "";
  }
}
