.sub-nav__list {
  margin: 0;
  padding: 0;

  .sub-nav__item {
    background-color: #213049;
    border-bottom: 0;

    .sub-nav__btn {
      width: 100%;
      padding: 0;
      border: 0;
      background-color: transparent;
      color: #fff;
      text-align: left;
      cursor: pointer;

      // &:hover,
      // &:focus,
      // &:active {
      //   background-color: #5cbcf0;
      //   font-weight: bold;
      // }
    }

    .sub-nav__name {
      display: block;
      position: relative;
      padding: 0.8rem 2rem 0.8rem 2.5rem;
      font-size: 1rem;
    }

    .sub-nav__name--isClicked {
      background-color: #151e2d;
      font-weight: bold;
    }

    & + .sub-nav__item {
      border-top: 1px solid #364864;
    }
  }
}
