.header__dropdown {
  position: absolute;
  z-index: 1;
  top: 100%;
  width: 100%;
  margin: 0;
  padding: 0;

  .header__dropdown-item {
    border-top: 1px solid #0d86d8;
    list-style: none;
    // color: #000;
    // height: 100%;
    .header__dropdown-btn {
      width: 100%;
      height: 4rem;
      padding: 0 0 0 1rem;
      border: 0;
      background-color: #1a93e5;
      font-size: 1rem;
      color: #fff;
      line-height: 4rem;
      list-style: none;
      text-align: left;
      cursor: pointer;

      &:hover,
      // &:focus,
      &:active {
        background-color: #0d86d8;
        color: #ddd;
      }
    }
  }
}
